.Advert{
    background-color: #fdecefcf;
    color: black;
    width: 100%;
    margin: auto;
    font-family: 'Merriweather', serif; /* Use Merriweather for the title */
    
}

.Advert .container{
      display: flex;
        justify-content: center;
        padding: 14px 12px;
        top: 0;
        z-index: 12;
        width: 100%;
        background-color: #fdecefcf;
        transition: 0.3s ease;
}

.advert-con span{
  letter-spacing: 3px;
    font-size: 11px;
    font-style: normal;
    color: rgba(0, 0, 0, 0.87);}

.Advert .container .login-con{
  display: flex;
  justify-content: center;
  align-items: center;
}

.Advert .container .login-con  span a{
        color: deeppink;
          font-size: 30px;
          margin-right: 20px;
}

.Advert .container .login-con  button {
        background-color: purple;
          height: 35px;
          width: 85px;
          border: none;
          outline: none;
          text-align: center;
          border-radius: 5px;
          color: white;
            text-decoration: none;
          opacity: 0.9;
          cursor: pointer;
          font-family: 'Playfair Display', sans-serif;
}

.Advert .container .login-con button:active{
  background-color: #3D5A5B;
    opacity: 0.9;
    cursor: pointer;
}

.Advert .container .login-con button a{
background-color: purple;
  height: 35px;
  width: 85px;
  border: none;
  text-decoration: none;
  outline: none;
  text-align: center;
  border-radius: 5px;
  color: white;
  opacity: 0.9;
  cursor: pointer;
  font-family: 'Playfair Display', sans-serif;
}

/* Login css*/

/* Google Fonts - Poppins */

.login{
  display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
      background-color: purple;
      transition: ease-in-out;
      z-index: 100;
      position: relative;
}

  .logindiv{
    display: flex;
    flex-direction: column;
    width: 100%;
    align-items: center;
    justify-content: center;
  }

  .login-con{
    width: 400px;
      margin-left: 5px;
      margin-right: 5px;  }

   .login-details{
      background-color: white;
      max-width: 500px;
      display: flex;
        flex-direction: column;
        width: 100%;
        align-items: center;
        justify-content: center;
        border-radius: 10px;
  }


  .login-details h2{
              font-family: 'Merriweather', serif;
                padding-top: 2rem;
                padding-bottom: 1rem;
                text-align: center;
                text-shadow: 1px 0px 0px rgb(0 0 0 / 84%);
  }

  .email-container, .password-input-container{
        position: relative;
          margin-bottom: 4px;
          margin-top: 2rem;}

      .email-container input, .password-input-container input{
    border-color: black;
      height: 35px;
      border-radius: 5px;
      border-width: 1px;
      width: 300px;
      padding-left: 30px;
      padding-right: 10px;
      font-size: 12px;
      position: relative;
      font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    }

      .email-container input::placeholder, .password-input-container input::placeholder{
        font-size: 12px;
        font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
      }

      .email-container span, .password-input-container .fakey{
      position: absolute;
        left: 6px;
        top: 11px;
        color: black;
        opacity: 0.6;
        font-size: 12px;;      }
      
      
      
      .password-input-container span{
            position: absolute;
              right: 5px;
              bottom: 11px;
              color: black;
              opacity: 0.7;
              font-size: 12px;
      }

   .validation-error{
      color: red;
        font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
        font-size: 13px;
        padding-left: 2px;
        margin-top: 5px;
  }

  .password-error{
        color: red;
          font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
          font-size: 13px;
          padding-left: 2px;
          margin-top: 5px;
  }

  .resetpasswordcon{
    display: flex;
    justify-content: center;
    align-items: center;
        font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
        margin-top: 0.5rem;
  }

  .resetpasswordcon p a{
    text-decoration: none;
    color: blue;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    font-size: 14px;
  }

  .btn-con{
    display: flex;
    flex-direction: column;
  }

  .btn-con button{
          border-color: purple;
            height: 40px;
            border-radius: 5px;
            border-width: 1px;
            outline: none;
            border: none;
            width: 300px;
            padding-left: 30px;
            color: white;
            padding-right: 10px;
            font-size: 14px;
            cursor: pointer;
            position: relative;
            background: purple;
            font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
            margin-top: 20px;
  }

  .registercontainer {
    display: flex;
    margin-top: 0.5rem;
  }

  .registercontainer p{
    font-size: 14px;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  }

  .registercontainer p a{
    text-decoration: none;
    color: blue;
    font-size: 14px;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  }

  .OR{
    text-align: center;
      margin-top: 20px;
      font-size: 15px;
    font-family: 'Merriweather', serif;
      /* Use Merriweather for the title */     }
  .google-div{
    display: flex;
    position: relative;
    padding-bottom: 2rem;
    padding-top: 1.5rem;
  }

  .google-div button{
      border-color: black;
        height: 40px;
        border-radius: 5px;
        border-width: 1px;
        border: none;
        cursor: pointer;
        width: 300px;
        padding-left: 30px;
        padding-right: 10px;
        font-size: 14px;
        position: relative;
        font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  }
  

  .google-div img{
      width: 15px;
        height: 15px;
        position: absolute;
        object-fit: cover;
        top: 36px;
        left: 85px;
  }


  @media screen and (max-width: 990px) {
  .Advert .container .login-con{
    display: none;
  }

